import Splide from '@splidejs/splide';


if (document.querySelector('#carousel')) {
  new Splide( '#carousel', {
    pagination: false,
    type: 'slide',
    perPage: 4,
    perMove: 1,
    gap: 32,
    breakpoints: {
      500: {
        perPage: 1,
        gap: 10,
        padding: { right: 30, left: 30 },
      },
      767: {
        perPage: 1,
        gap: 10,
        padding: { right: 30, left: 30 },
      },
      1024: {
        perPage: 2,
        gap: 16,
        padding: { right: 0, left: 0 },
      },
      1400: {
        perPage: 3,
        gap: 32,
      },
    }
  } ).mount();
}

if (document.querySelector('#testimonials')) {
  new Splide( '#testimonials', {
    pagination: false,
    type: 'slide',
    perPage: 3,
    perMove: 1,
    gap: 28,
    breakpoints: {
      500: {
        perPage: 1,
        gap: 10,
      },
      767: {
        perPage: 2,
        gap: 10,
      },
      1024: {
        perPage: 2,
        gap: 16,
      },
      1400: {
        perPage: 3,
        gap: 32,
      },
    }
  } ).mount();
}

if (document.querySelector('#logos')) {
  new Splide( '#logos', {
    pagination: true,
    type: 'slide',
    perPage: 10,
    destroy: true,
    mediaQuery: 'min',
    perMove: 1,
    breakpoints: {
      320: {
        perPage: 2,
        gap: 10,
        perMove: 1,
        destroy: false,
        padding: { right: 30, left: 30 },
      },
      500: {
        perPage: 3,
        gap: 10,
        perMove: 1,
        destroy: false,
        padding: { right: 30, left: 30 },
      },
      767: {
        destroy: true,
      },
    }
  } ).mount();
}
